import { MyTheme } from '@utils/interface';
import { theme } from '@utils/theme';
import React from 'react';
import styled, { ThemeProps, withTheme } from 'styled-components';
import { Anchor } from './anchor';

export interface ILink {
  label: string;
  url: string;
  newTab: boolean;
}

export interface INavProps extends Partial<MyTheme> {
  banner: React.ReactElement;
  bannerUrl: string;
  links: ILink[];
}

const NavWrapper = styled.ul<MyTheme>`
  list-style: none;
  margin: 0;
  padding: 15px;
  font-size: 24px;
  color: ${props => props.theme.colors.white};

  .banner-list {
    display: inline-block;
    margin-right: 120px;
  }
`;

const NavLink = styled.li`
  display: inline-block;
  margin-right: 30px;
`;

const CNav: React.FC<INavProps> = props => (
  <div>
    <Anchor href={props.bannerUrl}>
      <div style={{ height: 32 }}>{props.banner}</div>
    </Anchor>
    <NavWrapper>
      {props.links.map(link => (
        <NavLink key={link.label}>
          <Anchor href={link.url} highlight='#f2f2f2' withNextRoute={!link.newTab}>
            {link.label}
          </Anchor>
        </NavLink>
      ))}
    </NavWrapper>
  </div>
);

export const Nav = withTheme(CNav);
